import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { toJS } from 'mobx';
import { Text, Localizer } from 'preact-i18n';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(true);
const MARGIN_BOTTOM = mu.marginBottom();
const SUB_MENU_MARGIN_TOP = mu.subMenuMarginTop();

import Dog from '../../components/dog';
import DogAddLog from '../../components/dog/addlog';
import DogStatus from '../../components/dog/status';
import DogCalendar from '../../components/dog/calendar';
import Teams from '../../components/team/teams';
import Ad from '../../components/ad/';

const initialState = {
	isLoading: false,
	submenu: 'all',
	showDaysBackEdit: false,
	showAllDogs: false,
};

@observer
class Dogs extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	loadDogs = async (props = this.props) => {
		const { showAllDogs } = this.state;
		this.setState({ isLoading: true });
		const { dogStore, userStore } = this.props.stores;
		const { currentTeam, daysBack } = dogStore;
		const query = {
			team: currentTeam,
		};
		if (showAllDogs) {
			query.deceased = 1;
		}

		await Promise.all([
			this.loadWorkoutPlans(props),
			dogStore.load({ query, addData: ['workoutSummary', 'vaccineStatuses', 'historyStatuses'] }),
			dogStore.getDogsWithTraining({ daysBack }),
			userStore.getNotifications(),
		])
		this.setState({ isLoading: false });
	}

	loadWorkoutPlans = async (props = this.props) => {
		this.setState({ isLoading: true });
		const { year } = props;

		const currentMonth = new Date().getMonth() + 1;
		const currentYear = util.getYear();
		const seasonStart = parseInt(year || (currentMonth < 8 ? currentYear - 1 : currentYear), 10);
		const { workoutPlanStore, workoutStore } = props.stores;
		const { currentTeam } = workoutStore;

		workoutPlanStore.updateItem('workoutPlans', []);
		workoutPlanStore.updateItem('workoutPlan', {});
		workoutPlanStore.resetGraphPlanWeekDistanceCurrent();

		if (currentTeam) {
			await workoutPlanStore.load({ query: {
				team: currentTeam,
				seasonYear: seasonStart,
				seasonMonth: 6,
			} });
			const { workoutPlans } = workoutPlanStore;
			const workoutPlanCurrent = workoutPlans[0] || {};
			if (workoutPlanCurrent.id) {
				await workoutPlanStore.load(workoutPlanCurrent.id);
				workoutPlanStore.prepareWeekGraphs({ year });
			}
		}
		this.setState({ isLoading: false });
	}

	// async loadWorkoutSummary() {
	// 	const { userStore } = this.props.stores;
    //     await userStore.getWorkoutSummary({ daysBack: 2 });
    // }

	toggleDaysBackEdit = () => {
		const { showDaysBackEdit } = this.state;
		this.setState({
			showDaysBackEdit: !showDaysBackEdit,
		});
	}

	toggleShowAllDogs = () => {
		const { showAllDogs } = this.state;
		this.setState({
			showAllDogs: !showAllDogs,
		}, () => this.loadDogs());
	}

	setHistoryType = (historyType) => {
		this.setState({ historyType });
    }

	chooseSubmenu = (e) => {
		const submenu = e.target.parentElement.dataset.menu || e.target.dataset.menu;
		this.setState({ submenu });
	}

	chooseDaysBack = (e) => {
		const daysBack = parseInt(e.target.parentElement.dataset.daysback || e.target.dataset.daysback, 10);
		const { dogStore } = this.props.stores;
		dogStore.setDaysBack(daysBack);
		dogStore.getDogsWithTraining({ daysBack });
		this.toggleDaysBackEdit();
	}

	componentDidMount() {
		this.loadDogs();
		// this.loadWorkoutSummary();
	}

	render() {
		const { isLoading, historyType, submenu, showDaysBackEdit, showAllDogs } = this.state;
		const { dogStore, appState, userStore } = this.props.stores;
		const { daysBack, currentTeam } = dogStore;

        const { user } = userStore;
        const { language = 'no' } = user;
        const HISTORY_TYPE_MAP = mu.getHistoryMap(language);

		const { month, year } = this.props;

		const { darkmode } = appState;
		const { dogs, workoutSummaryCurrent } = dogStore;

		// const dogsWithStatus = dogStore.findDogsByStatus([2, 4, 8, 9]);
		// const dogsWithNeeds = dogStore.findDogsWithNeeds();

        let dogFilter = () => true;
        if (dogs) {
            switch (submenu) {
                case 'all':
					dogFilter = () => true;
                    break;
                case 'leadOnly':
                    dogFilter = (e) => {
                        if (e.positions && e.positions.lead && e.positions.lead.distanceKm > 0) {
                            return true;
                        }
                        return false;
                    };
                    break;        
                case 'pointOnly':
                    dogFilter = (e) => {
                        if (e.positions && e.positions.point && e.positions.point.distanceKm > 0) {
                            return true;
                        }
                        return false;
                    };
                    break;        
                case 'teamOnly':
                    dogFilter = (e) => {
                        if (e.positions && e.positions.team && e.positions.team.distanceKm > 0) {
                            return true;
                        }
                        return false;
                    };
                    break;        
                case 'wheelOnly':
                    dogFilter = (e) => {
                        if (e.positions && e.positions.wheel && e.positions.wheel.distanceKm > 0) {
                            return true;
                        }
                        return false;
                    };
                    break;        
            }
        }
        let filteredDogs = dogs.filter(dogFilter);
		// const dogIds = historyType ? userStore.findDogsFromHistory(historyType) : [];
		// const dogFilter = historyType ? d => dogIds.indexOf(d.id) > -1 : () => true;
		// const filteredDogs = dogs.filter(dogFilter);

		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
				<div class='row mb-4 fixed-top' style={`margin-top: ${SUB_MENU_MARGIN_TOP}; background-color: ${darkmode ? '#191d21' : '#f8f9fa'};`}>
					<div class='col text-center border-bottom border-primary subtopmenu'>
						<nobr><a href='/dogs' class='text-primary stretched-link'><i class='fas fa-chart-line' /> <Text id='dogs.dashboard'>Oversikt</Text></a></nobr>
					</div>
					<div class='col text-center subtopmenu'>
						<nobr><a href='/dogs/list' class='text-secondary stretched-link'><i class='fas fa-list' /> <Text id='dogs.list'>Liste</Text></a></nobr>
					</div>
					<div class='col text-center subtopmenu'>
						<nobr><a href='/dogs/feed' class='text-secondary stretched-link'><i class='fas fa-rss' /> <Text id='dogs.feed'>Feed</Text></a></nobr>
					</div>
					<div class='col text-center subtopmenu'>
						<nobr><a href='/dogs/todo' class='text-secondary stretched-link'><i class='fas fa-calendar-check' /> <Text id='dogs.todo'>Todo</Text></a></nobr>
					</div>
				</div>

				<div class='row'>
					<div class='col-12'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 mb-2 border-bottom'>
							<Teams stores={this.props.stores} callback={this.loadDogs} />
						</div>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 pb-2 clearfix'>
							<span class='float-right'>
								<a class='btn btn-sm btn-primary ml-2'
									href='/dogs/edit/new'
								>
									<i class='fas fa-plus' /> <Text id='dogs.add-dogs'>Legg til hunder</Text>
								</a>
							</span>
							<DogAddLog stores={this.props.stores} dog={{}} callback={this.loadDogs} />
						</div>

						{!isLoading && dogs && dogs.length === 0 && (
							<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 text-muted'>
								<div class='text-center mb-5'>
									<div class='display-1'>
										<i class='fas fa-dog' />
									</div>
									<h5>
										<Text id='dogs.no-dogs-registered'>Ingen hunder er registrert ennå.</Text>
									</h5>
								</div>
								<Text id='dogs.howto-add-1'>Slik kommer du i gang:</Text>
								<ol>
									<li><Text id='dogs.howto-add-2'>Trykk på knappen "Legg til hunder" rett nedenfor.</Text></li>
								</ol>
							</div>
						)}

						{/* {dogsWithStatus && dogsWithStatus.length > 0 && <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 mb-4 clearfix'>
							<h5><Text id='dogs.dog-with-follow-up'>Hunder som trenger oppfølging</Text></h5>
							<div class='row'>
								{dogsWithStatus && dogsWithStatus.map(d => <DogStatus stores={this.props.stores} dog={d} daysBack={daysBack} />)}
								{!dogsWithStatus || dogsWithStatus.length === 0 && <div class='col-12 text-center text-muted'>
									<h3><i class='fas fa-thumbs-up' /></h3>
									<Text id='dogs.no-dogs-with-follow-up'>Ingen hunder som trenger ekstra oppfølging.</Text>
								</div>}
							</div>
						</div>}

						{dogsWithNeeds && dogsWithNeeds.length > 0 && <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 mb-4 clearfix'>
							<h5><Text id='dogs.dog-with-special-needs'>Hunder med spesielle behov</Text></h5>
							<div class='row'>
								{dogsWithNeeds && dogsWithNeeds.map(d => <DogStatus stores={this.props.stores} dog={d} daysBack={daysBack} />)}
								{!dogsWithNeeds || dogsWithNeeds.length === 0 && <div class='col-12 text-center text-muted'>
									<h3><i class='fas fa-thumbs-up' /></h3>
									<Text id='dogs.no-dogs-with-special-needs'>Ingen hunder med spesielle behov.</Text>
								</div>}
							</div>
						</div>} */}

						{/* {workoutSummaryCurrent && workoutSummaryCurrent.length > 0 && <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 mb-4 clearfix'>
							<h5><Text id='dogs.dog-with-workouts-last-day'>Hunder som har trent siste døgn</Text></h5>
							<div class='row'>
								{workoutSummaryCurrent && workoutSummaryCurrent.map(summary => {
									const dog = dogStore.findDog(summary.dog);
									return (
										<DogStatus stores={this.props.stores} dog={dog} showWorkout={true} />
									); 
								})}
							</div>
						</div>} */}

						{/* <DogCalendar stores={this.props.stores} basehref={`/dogs/calendar/`} month={month} year={year} callback={this.setHistoryType} /> */}

						<div class='row'>
							<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 my-3'>
								<div class='row'>
									<div class='col px-1 pt-2'>
										<button
											class={`btn btn-block ${submenu === 'all' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
											style='text-overflow: ellipsis; overflow: hidden;'
											data-menu='all'
											onClick={this.chooseSubmenu}
										><i class='fas fa-tachometer-alt' /> <Text id='dogs.all'>Alle</Text></button>
									</div>
									<div class='col px-1 pt-2'>
										<button
											class={`btn btn-block ${submenu === 'leadOnly' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
											style='text-overflow: ellipsis; overflow: hidden;'
											data-menu='leadOnly'
											onClick={this.chooseSubmenu}
										><i class='fas fa-crosshairs' /> <Text id='dogs.leadOnly'>Leder</Text></button>
									</div>
									<div class='col px-1 pt-2'>
										<button
											class={`btn btn-block ${submenu === 'pointOnly' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
											style='text-overflow: ellipsis; overflow: hidden;'
											data-menu='pointOnly'
											onClick={this.chooseSubmenu}
										><i class='fas fa-crosshairs' /> <Text id='dogs.pointOnly'>Point</Text></button>
									</div>
									<div class='col px-1 pt-2'>
										<button
											class={`btn btn-block ${submenu === 'teamOnly' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
											style='text-overflow: ellipsis; overflow: hidden;'
											data-menu='teamOnly'
											onClick={this.chooseSubmenu}
										><i class='fas fa-crosshairs' /> <Text id='dogs.teamOnly'>Team</Text></button>
									</div>
									<div class='col px-1 pt-2'>
										<button
											class={`btn btn-block ${submenu === 'wheelOnly' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
											style='text-overflow: ellipsis; overflow: hidden;'
											data-menu='wheelOnly'
											onClick={this.chooseSubmenu}
										><i class='fas fa-crosshairs' /> <Text id='dogs.wheelOnly'>Wheel</Text></button>
									</div>
								</div>
							</div>
						</div>

						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 my-3'>
							{showDaysBackEdit && <div class='row'>
								<div class='col px-1 pt-2'>
									<button
										class={`btn btn-block ${daysBack === 0 ? 'btn-warning' : 'btn-outline-warning'} text-nowrap`}
										style='text-overflow: ellipsis; overflow: hidden;'
										data-daysback='0'
										onClick={this.chooseDaysBack}
									><Text id='dogs.d1'>Dag</Text></button>
								</div>
								<div class='col px-1 pt-2'>
									<button
										class={`btn btn-block ${daysBack === 1 ? 'btn-warning' : 'btn-outline-warning'} text-nowrap`}
										style='text-overflow: ellipsis; overflow: hidden;'
										data-daysback='1'
										onClick={this.chooseDaysBack}
									><Text id='dogs.d2'>2 dager</Text></button>
								</div>
								<div class='col px-1 pt-2'>
									<button
										class={`btn btn-block ${daysBack === 7 ? 'btn-warning' : 'btn-outline-warning'} text-nowrap`}
										style='text-overflow: ellipsis; overflow: hidden;'
										data-daysback='7'
										onClick={this.chooseDaysBack}
									><Text id='dogs.d7'>7 dager</Text></button>
								</div>
								<div class='col px-1 pt-2'>
									<button
										class={`btn btn-block ${daysBack === 14 ? 'btn-warning' : 'btn-outline-warning'} text-nowrap`}
										style='text-overflow: ellipsis; overflow: hidden;'
										data-daysback='14'
										onClick={this.chooseDaysBack}
									><Text id='dogs.d14'>14 dager</Text></button>
								</div>
								<div class='col px-1 pt-2'>
									<button
										class={`btn btn-block ${daysBack === 30 ? 'btn-warning' : 'btn-outline-warning'} text-nowrap`}
										style='text-overflow: ellipsis; overflow: hidden;'
										data-daysback='30'
										onClick={this.chooseDaysBack}
									><Text id='dogs.d30'>30 dager</Text></button>
								</div>
							</div>}
						</div>

						<Ad stores={this.props.stores} url={this.props.url} />

						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 mb-4 clearfix'>
							<h5 class='font-weight-light'>
								<span class='float-right' onClick={this.toggleDaysBackEdit}>
									<span class='text-muted mr-2'><Text id='dogs.days-back'>Siste</Text>:</span>
									{daysBack === 0 ? <Text id='dogs.d1'>Dag</Text> : ''}
									{daysBack === 1 ? <Text id='dogs.d2'>2 dager</Text> : ''}
									{daysBack === 7 ? <Text id='dogs.d7'>7 dager</Text> : ''}
									{daysBack === 14 ? <Text id='dogs.d14'>14 dager</Text> : ''}
									{daysBack === 30 ? <Text id='dogs.d30'>30 dager</Text> : ''}
								</span>
								{historyType ? (<span>
									<Text id='dogs.dogs-filtered-by'>Filtrert på:</Text> {HISTORY_TYPE_MAP[historyType]}
								</span>) : (<span>
									<Text id='dogs.all-dogs' fields={{ total: filteredDogs.length }}>Alle hundene - {filteredDogs.length} stk</Text>
								</span>)}
							</h5>
							<div class='row'>
								{filteredDogs && filteredDogs.map(d => {
									const summary = dogStore.findCurrentWorkoutSummary(d.id) || {};
									const totalDistance = summary.distanceKm;
									return (
										<DogStatus stores={this.props.stores}
											key={`dog-${d.id}-${currentTeam}-${workoutSummaryCurrent.length}-${totalDistance}`}
											dog={d}
											hideTeam={true}
											showWorkout={false}
											daysBack={daysBack}
											filterBy={submenu}
										/>
									);
								})}
							</div>
							<span
								class='pointerCursor ml-2 font-weight-lighter float-right'
								onClick={this.toggleShowAllDogs}
							>
								{showAllDogs ? <>
									<Text id='dogs.hide-deceased'>Vis kun nåværende team</Text>
								</> : <>
									<Text id='dogs.absolutely-all'>Vis absolutt alle</Text>
								</>}
							</span>
						</div>


					</div>
				</div>
			</div>
		);
	}
}

export default Dogs;
